#photo{
    height: 474px;
    width: 714px;
    /* background-color: aqua; */
    margin: auto;
    margin-top: 80px;
    margin-bottom: 10px;
    background-image: url(./endpic.png);
    background-size: cover;
}

.photo{
    text-align: center;
}

.photo svg{
    padding-left: 5px;
    vertical-align: text-bottom;
    color:#666666;
}


.photo a{
    color: black;
    text-decoration: none;
    font-family: 'Times New Roman', Times, serif;
    font-size: 15px;
}

.photo a:hover {
    color:#756A64; 
    text-decoration:none; 
    cursor:pointer;  
}